import React, { FunctionComponent } from 'react'
import { donations } from '../../data/index'
const Donators: FunctionComponent = () => (
  <div className="donators text-left bg-gray-100 rounded-sm py-6 px-16">
    <h4 className="text-xl font-medium text-gray-800">Ë¥¢ÊîøË¥°ÁåÆËÄÖÊéíË°å</h4>
    <h5 className="text-gray-500 mt-4">ÁªÑÁªá</h5>
    <ol className="mt-2 grid grid-cols-2 gap-y-2">
      {
        donations.map((item, index) => (
          <li key={index} className="flex">
            <div className="flex items-center h-8 justify-between pl-4 pr-1 bg-white rounded-3xl">
              <div className="text-xs text-gray-400">{index + 1}</div>
              <img src={item.icon} alt={item.name} className="w-7 ml-3"/>
            </div>
            <div className="ml-2">
              <h6 className="text-base font-medium">{item.name}</h6>
              <div className="text-xs text-gray-500">{moneyFormat(item.amount)}</div>
            </div>
          </li>
        ))
      }
    </ol>
  </div>
)
function moneyFormat (money: number) {
  if (money >= 10**3) {
    return `$${Math.floor(money / 1000)}k USD`
  } else {
    return `$${money} USD`
  }
}
export default Donators